import { BrowserModule } from '@angular/platform-browser';
import { NgModule,enableProdMode } from '@angular/core';
import { MyService } from './my.service';
import { AuthGuard } from './auth.guard';
import { AuthInterceptor } from './auth.interceptor';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import {HttpClientModule,HTTP_INTERCEPTORS, HttpClient } from '@angular/common/http';
import {TranslateModule, TranslateLoader} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import { ToastComponent } from './components/toast/toast.component';
import { routes } from './app-routing.module';
import { KeyboardComponent } from './components/keyboard/keyboard.component';
import { DropdownComponent } from './components/dropdown/dropdown.component';

import { FilterPipeModule } from 'ngx-filter-pipe';

import { CurrencyPipe,HashLocationStrategy, LocationStrategy } from '@angular/common';
import { AppComponent } from './app.component';



import { IndexComponent } from './pages/index/index.component';
import { RunnerComponent } from './pages/runner/runner.component';
import { KitchenComponent } from './pages/kitchen/kitchen.component';
import { RunnerViewComponent } from './pages/runner-view/runner-view.component';
import { RunnerSearchComponent } from './pages/runner-search/runner-search.component';
import { KitchenSearchComponent } from './pages/kitchen-search/kitchen-search.component';
import { KitchenViewComponent } from './pages/kitchen-view/kitchen-view.component';
import { MyClockDirective } from './directives/my-clock.directive';

import { RouterModule} from "@angular/router";

import { DropdownbComponent } from './components/dropdownb/dropdownb.component';
import { KeysPipe } from './pipes/keys.pipe';
import { DelayDirective } from './directives/delay.directive';
import { SearchDropComponent } from './components/search-drop/search-drop.component';
import { DebounceClickDirective } from './directives/debounce-click.directive';



export function HttpLoaderFactory(http:HttpClient) {
    return new TranslateHttpLoader(http, "./assets/i18n/", ".json");
}
enableProdMode();
@NgModule({
  declarations: [
    AppComponent,
    ToastComponent,
    RunnerComponent,
    KitchenComponent,
    KeyboardComponent,
    DropdownComponent,
    IndexComponent,
   
    RunnerViewComponent,
    RunnerSearchComponent,
    KitchenSearchComponent,
    KitchenViewComponent,
    MyClockDirective,
  
    DropdownbComponent,
    KeysPipe,
    DelayDirective,
    SearchDropComponent,
    DebounceClickDirective
    
    
   
  ],
  imports: [
    BrowserModule,
    FormsModule ,
    ReactiveFormsModule,
    HttpClientModule,
    FilterPipeModule,
    
    TranslateModule.forRoot({
    loader: {
      provide: TranslateLoader,
      useFactory: HttpLoaderFactory,
      deps: [HttpClient],
    }
   }),
    RouterModule.forRoot(routes)
  ],
 

  providers: [CurrencyPipe, {provide: LocationStrategy, useClass: HashLocationStrategy},{
     provide: HTTP_INTERCEPTORS,
     useClass: AuthInterceptor,
     multi: true
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }
