import { Component, OnInit ,ElementRef,ViewChild,ViewChildren,QueryList} from '@angular/core';
import { FormGroup, FormControl, FormArray, FormBuilder ,Validators} from '@angular/forms';
import {Router,ActivatedRoute}  from '@angular/router';
import { MyService } from '../../my.service';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { Observable,fromEvent,merge,} from 'rxjs';
import { fromArray } from 'rxjs/observable/fromArray';
import { map, throttleTime,debounceTime, scan,filter,first,buffer,take,debounce,throttle,bufferTime} from 'rxjs/operators';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { DropdownComponent } from '../../components/dropdown/dropdown.component';
@Component({
  selector: 'app-kitchen',
  templateUrl: './kitchen.component.html',
  styleUrls: ['./kitchen.component.scss']
})
export class KitchenComponent implements OnInit {
  
   showKey:Boolean=false;
   host:String="";
   title:String="xxxx";
   scrollWidth:any=1024;
   screenWidth:any=1024;
   Number:any;
   Math:any;
   total:any=1;
   page:any=1;
   testJSON:Object={2: 'foo', 1: 'bar'};
   isShowAll:Boolean=false;
   orders:Array<Object>=[];
   summary:Array<Object>=[];
    @ViewChild("mainRef") mainRef: ElementRef;
    ele:any ;
    key:any;
    @ViewChildren("sectionRef") sectionRef;
    @ViewChild(DropdownComponent)   columnCom:DropdownComponent;
 /*   @ViewChild(DropdownComponent)   fontCom:DropdownComponent;*/
    @ViewChildren(DropdownComponent) vc: QueryList<DropdownComponent>;
 
   column:Object={"value":"3","_id":"3"}
   font:Object={"value":"1","_id":"1"}
   dateTime:any=""; 
   licenseKey:String="";
 /*  @ViewChild("licenseKeyRef") licenseKeyRef: ElementRef;*/
  constructor(public translate: TranslateService,public myService:MyService,
    public router:Router,public elementRef:ElementRef,public xml2json: NgxXml2jsonService,public route:ActivatedRoute 
    ) {
  	this.Number=Number;
    this.Math=Math;
  }
   init(){
  this.column=window.localStorage.getItem("COLUMN")?JSON.parse(window.localStorage.getItem("COLUMN")):{"value":"3","_id":"1"};
  this.font=window.localStorage.getItem("FONT")?JSON.parse(window.localStorage.getItem("FONT")):{"value":"1","_id":"2"};
  
  this.myService.screenWidth=this.mainRef.nativeElement.clientWidth;
  this.licenseKey=window.localStorage.getItem("kitchenLicense");
    }
  
  blurColumn(){setTimeout(()=>{this.vc["_results"][0].toggle(false)},200)}
  
  columnEvent(event){
      this.column=event;
    this.checkedPage(false);
    window.localStorage.setItem("COLUMN",JSON.stringify(event));
    this.vc["_results"][0].toggle(false)
  }
  clickColumn(){this.vc["_results"][0].toggle();}

  blurFont(){setTimeout(()=>{this.vc["_results"][1].toggle(false)},200)}
  fontEvent(event){

  window.localStorage.setItem("FONT",JSON.stringify(event));
  this.checkedPage(false);  
  
  this.font=event;
  this.vc["_results"][1].toggle(false)
}
  clickFont(){this.vc["_results"][1].toggle()}

  done(){

  }

  refresh(){
    window.location.reload();
  }
  ngOnInit() {
     this.init();
     const keyup$=fromEvent(document,"keyup");
     let debounceMouseBreak$ = keyup$.pipe(debounceTime(300));
    const scroll$=fromEvent(this.mainRef.nativeElement,"scroll");
    scroll$.pipe(throttleTime(300)).subscribe(event=>{
            let startValue=this.mainRef.nativeElement.scrollLeft%this.myService.screenWidth;
              let endValue=startValue;
              if(startValue>0){
                
                 endValue=this.myService.screenWidth-startValue;
                  if(this.key=="n"){
                  this.mainRef.nativeElement.scrollLeft=this.mainRef.nativeElement.scrollLeft+endValue;  
                   }else if(this.key=="p"){
                     this.mainRef.nativeElement.scrollLeft=this.mainRef.nativeElement.scrollLeft-startValue;
                   }
  
              }
           

    })

    const that=this;
    keyup$.pipe(buffer( debounceMouseBreak$)).subscribe(event=>{
      this.key=event[0]['key'];
    if(this.key=="Enter"){
       this.setSummary();
      	return true ;
      }
      let maxPage=Math.ceil(this.mainRef.nativeElement["scrollWidth"]/this.myService.screenWidth)-1;
      if(isNaN(this.key)){
          if(document.activeElement && document.activeElement["tagName"]!="SECTION")this.firstFocus();  
      }else if(this.key>maxPage){
         this.key=maxPage;
      }
     if(this.key=="n"){
        this.ele=document.activeElement.nextElementSibling;
      }else if(this.key=="p"){
        
             this.ele=document.activeElement.previousElementSibling;
         
       }else if(this.key=="i"){
       
         if(this.elementRef.nativeElement.querySelector(":focus section")){
           let all=this.elementRef.nativeElement.querySelectorAll(":focus section");
           for(const ii in all){
             let ract=all[ii].getBoundingClientRect();
             if(ract.top>0){
               this.ele=all[ii];
               break;
             }
           }
          }else{
         
          this.ele=document.activeElement.parentElement;
          this.ele.focus();
        }
       }else if(this.key=="u"){
         this.undo();
         return true;
       }else if(this.key=="b"){
           let e= document.activeElement,temp={"pstStatus":"WAITING"};
           
            this.ele=e.nextElementSibling || e.previousElementSibling;
            if(this.ele){this.ele.focus()}else{
              if(document.activeElement && document.activeElement["tagName"]!="SECTION")this.firstFocus();
            }
        
           if(e.getAttribute("data-pstid")){
             temp["pstId"]=e.getAttribute("data-pstid");
             this.process(temp,e.getAttribute("data-orderid"));
           
           }else{
                 temp["pstId"]=[];
                 for(let v of this.myService.orders){
                      if(v["pstStatus"]=="WAITING" && v['orderId']==e.getAttribute("data-orderid")){
                         for(let vv of v["itemDisplayInfoList"]){
                                 if(vv["pstStatus"]=="WAITING"){
                                     temp["pstId"].push(vv["pstId"]);
                                 }
                         }
                        temp["orderId"]=e.getAttribute("data-orderid");  
                        this.batchProcess(temp);
                        setTimeout(arg=>{
                          if(document.activeElement && document.activeElement["tagName"]!="SECTION")this.firstFocus();  
                        },500)
                        
                        break;
                     }
                 }
           }
         
           
           return true;
       }else  if(this.key=="z"){
            location.reload(true);
       } else if(this.key=="k" || this.key=="q"){

         
           this.ele=document.activeElement.parentElement;
          if(this.ele)this.ele.focus();


         let top=300;
        if(this.key=="q")top=-300;
        document.activeElement.scrollTop=document.activeElement.scrollTop+top;

            return true;
       }else if(this.key=="1"){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="2" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="3" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="4" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="5" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="6" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="7" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }else if(this.key=="8" && this.key<=maxPage){
         this.nextPage(this.key,true);
         return;
       }
         if(this.ele)this.ele.focus();
           if(document.activeElement && document.activeElement["tagName"]=="SECTION"){
            if(document.activeElement.nextElementSibling && this.ele){
               this.ele.focus();//this.key="n";
             }else if(document.activeElement.previousElementSibling && this.ele){
               this.ele.focus();//this.key="p";
             }
            }
   
        
    })


    }
submit() {
  
}
undo(){
  this.myService.sendMessage({"sign":"UNDO"});
}
 setSummary(){
   this.isShowAll=!this.isShowAll;

 }
 process(item,orderId){
   
    let tempItem=JSON.parse(JSON.stringify(item));
    tempItem["orderId"]=orderId;
    tempItem["pstId"]=[tempItem.pstId];
   this.myService.sendMessage(tempItem);
 }
  batchProcess(obj){
   
/*    let tempItem=JSON.parse(JSON.stringify(item));
    tempItem["orderId"]=orderId;
    tempItem["pstId"]=[tempItem.pstId];*/
   this.myService.sendMessage(obj);
 }
 checkedPage(arg=true){
   
   /* if(arg){
    this.myService.screenWidth=this.mainRef.nativeElement.clientWidth;
     
   }
   */
        this.scrollWidth=this.mainRef.nativeElement.scrollWidth;
        this.total=Math.floor(this.scrollWidth / this.myService.screenWidth);
         this.page=1+(Math.floor(this.mainRef.nativeElement.scrollLeft/this.scrollWidth));
         this.mainRef.nativeElement.scrollLeft=(this.page-1)*this.scrollWidth;
         setTimeout((()=>{
              if(this.sectionRef.first && this.sectionRef.first.nativeElement)
              this.sectionRef.first.nativeElement.focus();
         }).bind(this),300)
        
    
 }
 ngAfterViewInit(){
  setTimeout(arg=>{ 
     if(this.sectionRef && this.sectionRef.first)this.sectionRef.first.nativeElement.focus();
       this.myService.screenWidth=document.querySelector("main").clientWidth;
   },1200)
    
 }
 exit(){
   window.parent.postMessage("exit","*")
 }
 isElementInViewport (el) {
    var rect = el.getBoundingClientRect();
    return (
        
        rect.left >= 0  && rect.bottom <= (window.innerHeight || document.documentElement.clientHeight)
       
    );
}
firstFocus(){
   for(let i in  this.sectionRef["_results"]){
            if(this.isElementInViewport(this.sectionRef["_results"][i]["nativeElement"])){
              this.sectionRef["_results"][i]["nativeElement"].focus();
              break;
            }
        }  
}
 nextPage(arg,sign=false){
     if(sign){
    this.page=arg;
     this.mainRef.nativeElement.scrollLeft=(this.page-1)*this.myService.screenWidth;
   }else{
      this.page=Math.ceil(this.mainRef.nativeElement.scrollLeft/this.myService.screenWidth)+1+arg;
      this.mainRef.nativeElement.scrollLeft=(this.page-1)*this.myService.screenWidth;
   }
    this.firstFocus();
   }
 language(){
        if(this.translate.currentLang=="en"){
           this.translate.use('zh-cn');
           this.translate.currentLang="zh-cn";
      }else{
           this.translate.use('en');
           this.translate.currentLang="en";

      }
      
      window.localStorage.setItem("LG",this.translate.currentLang);
      
    }
 }
