import { Directive, EventEmitter, HostListener, OnInit, Output, Input } from '@angular/core';
@Directive({
    selector: '[appDebounceClick]'
})
export class DebounceClickDirective implements OnInit {
    clearTime:any;
    timeOut:number=300;
    constructor() { }
    ngOnInit() {
     
    }
    @HostListener('click', ['$event'])
    clickEvent(event) {
    	window.clearTimeout(this.clearTime);
    	this.clearTime=setTimeout(arg=>{
    		event.target.setAttribute('style', 'pointer-events:auto');
    	},this.timeOut)
    	event.target.setAttribute('style', 'pointer-events: none');

    }
 }