import { Directive,Input,ElementRef } from '@angular/core';
import * as moment from 'moment';
@Directive({
  selector: '[appDelay]'
})
export class DelayDirective {

  	moment:any;	
    @Input() time: any=0;
    @Input() endTime:String="15,20";
    @Input() format: string='h:mm:ss A';
    @Input() stop:Boolean=false;
    clearTime:any;
  constructor(private el:ElementRef) {
     this.moment=moment;
   }
 ngAfterViewInit(){
        let temp=0;
        let end=this.endTime.split(",");
        temp=new Date().getTime()-this.time;
        let bgColor="";
        this.clearTime=setInterval(arg=>{
        if((temp/1000/60)>parseInt(end[1])){
		         	bgColor="rgb(255, 192 ,203) 0px 0px 0px 100px inset";
		 }else if((temp/1000/60)>parseInt(end[0])){
		         bgColor="rgb(245 ,245, 220) 0px 0px 0px 100px inset";
		  }
	   			temp+=1000;
	   			if(bgColor){
	   				if(this.el.nativeElement.style["box-shadow"]!=bgColor){
						this.el.nativeElement.style.cssText="box-shadow:"+bgColor;
	   					if(bgColor=="rgb(138, 194, 238) 0px 0px 0px 100px inset"){
	   						window.clearInterval(this.clearTime);
	   					}
	   				}	 
	   			}
	   		
	   		  },1000)

     	
   	    
   }
   pad2(num, n) { 
     return (Array(n).join('0') + num).slice(-n); 
   } 
  countDown = (second) => {
  	let time=second;
        let d=~~(time/(60*60*1000*24))
        let h=~~(time/(60*60*1000)%24);
     	let m=~~(time/(60*1000)%60);
        let s=~~(time/(1000)%60);
        return (d>0?d+" ":"")+(h>0?h+":":"")+this.pad2(m,2)+":"+this.pad2(s,2);
};
}