import { Component,Input, Output,OnInit ,ViewChild,ElementRef,EventEmitter} from '@angular/core';
@Component({
  selector: 'app-dropdownb',
  templateUrl: './dropdownb.component.html',
  styleUrls: ['./dropdownb.component.scss']
})
export class DropdownbComponent implements OnInit {
  @ViewChild("inputRef") inputRef:ElementRef;
 constructor() { 

   this.returnEvent = new EventEmitter<Object>();}
   @Input() list:Array<Object>=[];
   @Input() settings:Object={};
   isShow:Boolean=false;
  @Output() returnEvent:EventEmitter<Object>;
  ngOnInit() {
  }
  blur(){
   this.isShow=false;
  }
  toggle(event){
    this.isShow=!this.isShow;
  }
  selectItem(item){
 this.returnEvent.emit(item);
  
  }
}
