import { Component, OnInit,Input,EventEmitter,Output } from '@angular/core';

@Component({
  selector: 'app-dropdown',
  templateUrl: './dropdown.component.html',
  styleUrls: ['./dropdown.component.scss']
})
export class DropdownComponent implements OnInit {
  @Input() title:String;
  @Input() bg:String;
  isShow:Boolean=false;
  @Input() list:Array<Object>=[];
  @Output() returnEvent:EventEmitter<Object>;
    constructor() {
    this.returnEvent = new EventEmitter<Object>();
   }

  ngOnInit() {
  }
   toggle(sign=null){
      if(sign==null){this.isShow=!this.isShow}
      else{this.isShow=sign}
   
    
  }

  select(item){
  	  
  		this.returnEvent.emit(item);
  		

  }
/*  onBlur(){
  	
  	setTimeout((function(){
  		console.log("xxxxxxxxxxx")
  		this.isShow=false;
  	}).bind(this),200)
  	
  }*/

}
