import { Component, OnInit,ViewChild,ElementRef,Renderer} from '@angular/core';
import { MyService } from '../../my.service';
import * as moment from 'moment';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { Observable,Subscription  } from 'rxjs';
import { WebSocketSubject } from 'rxjs/observable/dom/WebSocketSubject';
import {Router,ActivatedRoute}  from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
/*export class Message {
    constructor(
        public sender: string,
        public content: string,
        public isBroadcast = false,
    ) { }
}*/
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
   moment:any;
   url:any="";
   licenseKey:string="";
   showKey:Boolean=false;
   printer:String="";
   subscription: Subscription;
   socket:any;
   
   screenWidth:any=1024;
   alert:any;
    doStatus:Object={"WAITING":"PROCESSING","PROCESSING":"SERVED","SERVED":"COMPLETE","INIT":"WAITING"};
    status:Object={"WAITING":1,"PROCESSING":2,"SERVED":3,"COMPLETE":4};
    w:any;
    @ViewChild("licenseKeyRef") licenseKeyRef: ElementRef;
   
 constructor(public translate: TranslateService,public myService:MyService,public renderer:Renderer,
    private xml2json: NgxXml2jsonService,private router:Router,private aroute:ActivatedRoute 
  	) {
    this.myService.host=window.location.host;
    this.url=this.router.url.indexOf("kitchen")>=0?"kitchen":"runner";
 }


   returnEvent(event){
   if (event==false) {
		        
		} else if (event=="Backspace") {
		   let len=this.licenseKey.length;
		         if(len>0)
		         this.licenseKey=this.licenseKey.substr(0,len-1)
		} else {
		    this.licenseKey+=event;
		}
		   this.licenseKeyRef.nativeElement.focus();
 }
  
 async checkLicense(){
     
        this.licenseKey=this.licenseKey || (window.localStorage.getItem(this.url+"License") || "");
        if(!this.licenseKey){this.showKey=true}else{

          if(this.licenseKey.toLowerCase()=="server"){
                     this.showKey=true
                     this.licenseKey="";
                     return true;
          }

           window.localStorage.setItem(this.url+"License",this.licenseKey);

           let sessionKey=await this.getSesstionKey(this.myService.host,this.licenseKey);

           let obj = this.xml2json.xmlToJson(sessionKey);
             obj=this.myService.getJsonValue(obj,"ClientInstanceLoginResponseType");
  
        
           if(obj["Result"] && obj["Result"]["successful"]=="true"){
                let xml='<soapenv:Envelope xmlns:soapenv="http://schemas.xmlsoap.org/soap/envelope/" xmlns:app="http://ws.kpos.com/app"> <soapenv:Header/> <soapenv:Body> <app:FindAppInstancesType><app:name>' + this.licenseKey + '</app:name> <app:type>KITCHEN_DISPLAY</app:type></app:FindAppInstancesType></soapenv:Body> </soapenv:Envelope>';
                let type="FindAppInstancesResponseType";
                let sendData={"data":xml,"method":"POST"};
                let result=await this.myService.soap(sendData,type);
                 
                 if(result["Result"]["successful"]=="true"){
                           let appInstances=result["appInstances"];
                               this.myService["appInstances"]["enableWait"]=eval(appInstances["enableWait"]);
                               this.myService["appInstances"]["modifyWait"]=eval(appInstances["modifyWait"]);
                                    if(appInstances["enableServe"]=="true"){
                                           this.myService["appInstances"]["filter"]={'pstStatus':{$or:["WAITING","PROCESSING","SERVED"]}}
                                           this.myService["appInstances"]["filterView"]={'pstStatus':{$or:["COMPLETE"]}}
                                       }
                                   if(appInstances["forewarn"]){
                                      this.myService["delayTime"]=appInstances["forewarn"];
                                   }
                                    this.printer=this.url=="kitchen"?appInstances["printerID"]:appInstances["displaysName"];
                                   if(typeof this.printer=="object")this.printer="";

                                   if(this.printer){
                                      this.getOrder(obj["sessionKey"]);  
                                    }else{
                                      this.showKey=false;
                                      this.myService.openAlert({"msg":"Please setting printer!"});
                                    }
                            }else{
                            this.myService.openAlert({"msg":result["Result"]["failureReason"]});
                       }
           }else{
             this.myService.openAlert({"msg":obj["Result"]["failureReason"]});
              this.showKey=true;
           }
         
      }
 }
  ngOnInit() {
        if(window.sessionStorage.getItem("exit")){
          this.myService.exit=true;
        }  
      this.renderer.listen(window, 'message', (event) => {
          
           event.source.postMessage("finish","*");
           this.myService.exit=true;
           window.sessionStorage.setItem("exit","true")
          
       }); 
   let lg=window.localStorage.getItem("LG") || "en";
   this.translate.use(lg);
   this.alert= new Audio('./assets/img/alert.wav');
    this.printer=this.aroute.snapshot["queryParams"]["licenseName"]; 
     if(this.printer!=undefined){
       this.myService["licenseKey"]="SERVER";
       this.licenseKey="SERVER";
       this.myService.appInstances["filter"]={'pstStatus':{$or:["WAITING","PROCESSING","SERVED"]}};
       this.myService.appInstances["filterView"]={'pstStatus':{$or:["COMPLETE"]}}
       this.getOrder();
     }else{this.checkLicense()}
       if(window.localStorage.getItem(this.url+"Undo")){
            this.myService["undoObj"]=JSON.parse(window.localStorage.getItem(this.url+"Undo"));
          }
          if(this.myService["undoObj"]=="undefined"){
           this.myService["undoObj"]=[];
          }
      
      this.subscription = this.myService.getMessage().subscribe(obj => {
              let sendData={"url":"/kpos/webapp/kitchen/kitchenDisplay","method":"put"};
              let tempData={};
              if(obj["sign"]){
                    if(this.myService["undoObj"].length>0){
                          sendData["data"]=this.myService["undoObj"][0];
                     }else{
                       this.myService.openAlert({"msg":"Undo operating limits"});
                       return true;
                      }
                 }else{
                       sendData["data"]={"status":this.doStatus[obj['pstStatus']],
                       "prepareToServeIds":obj['pstId'],"orderId":obj["orderId"]};

                      tempData={"status":obj['pstStatus'],
                     "prepareToServeIds":obj['pstId'],"orderId":obj["orderId"]}
                    }
                  

                    this.myService.service(sendData,false,true).subscribe(arg=>{
                           if(!arg["msg"] && arg["Successful"]==true){
                             if(tempData['status']!="INIT"){


                                if(obj["sign"]){
                                    this.myService["undoObj"].shift();
                                    window.localStorage.setItem(this.url+"Undo",JSON.stringify(this.myService["undoObj"]));
                                }else{
                                   this.myService["undoObj"].unshift(tempData);
                                    if(this.myService["undoObj"].length>5)
                                     this.myService["undoObj"].length=5;
                                     window.localStorage.setItem(this.url+"Undo",JSON.stringify(this.myService["undoObj"]));
                               } 
                             }
                       }
            
                   })  
               
                   

                   
      });

    
   }
  setSummary(){
      let searchStatus="WAITING";
      if(this.router.url.indexOf("runner")>=0)searchStatus+=",PROCESSING";
      this.myService["searchKey"]={};
      this.myService["summary"]={};
      for(let v of  this.myService["orders"]){
            if(v["itemDisplayInfoList"].length>0){
             v["pstStatus"]=v["itemDisplayInfoList"][0]["pstStatus"];
             v["status"]=v["itemDisplayInfoList"][0]["pstStatus"]; 
         }
        for(let vv of v["itemDisplayInfoList"]){
          if(searchStatus.indexOf(vv["pstStatus"])>=0){
             this.myService["searchKey"][vv["itemName"]]=vv["itemName"];

            if(this.myService["summary"][vv["itemName"]]){

               this.myService["summary"][vv["itemName"]]+=vv["qty"];
             }else{
               this.myService["summary"][vv["itemName"]]=vv["qty"];  
             }
           }
                 if(this.status[v["pstStatus"]]>this.status[vv["pstStatus"]])v["pstStatus"]=vv["pstStatus"];  
                 if(this.status[v["status"]]<this.status[vv["pstStatus"]])v["status"]=vv["pstStatus"];   
            }
 
}
  setTimeout(arg=>{
     let ele=document.querySelector("main");
     let total=Math.ceil(ele.scrollWidth/this.myService.screenWidth)-1;
     let curPage=Math.ceil(ele.scrollLeft/this.myService.screenWidth)+1;

     if(curPage>total){
       ele.scrollLeft=(total-1)*this.myService.screenWidth;
     }
   
    },300)
 
    //console.log(this.myService.orders)
  } 
  getOrder(sessionKey="QlK4V79OeJjyaxb1y2rl"){
  let sendData={"url":"/kpos/webapp/orderTypes","method":"get"};
   this.myService.service(sendData).subscribe(arg=>{
      if(!arg["msg"]){
        for(let v of arg["orderTypeSettingList"]){
           if(v["name"] !="DINE_IN" && v["name"] !="DELIVERY" && v["name"] !="PICKUP" && v["name"] !="TOGO"){
                  for(let vv of v["fieldDisplayNameGroups"][0]["fieldDisplayNames"]){
                         this.myService["orderType"][v["name"]]=this.myService["orderType"][v["name"]] || {};
                         this.myService["orderType"][v["name"]][vv["languageCode"]]=vv["name"];
                       }
             }
          }
      }
    
   }) 
 sendData={"url":"/kpos/webapp/kitchen/kitchenDisplay/"+this.printer,"method":"get"};
   this.myService.service(sendData).subscribe(arg=>{
      if(!arg["msg"]){
       this.myService.orders=arg["orderDisplayList"];
       this.openSocket({"sessionKey":sessionKey});
       this.setSummary()}
    
   }) 

  }

  openSocket(obj){
    if(<any>WebSocket ){
      const that=this;
  	 	console.log('ws://' +this.myService.host + '/kpos/webapp/webSocket/systemInfo');
  		that.socket=new WebSocket('ws://' +this.myService.host + '/kpos/webapp/webSocket/systemInfo');	

            that.socket.onopen = function() {
                console.log("open");
                that.socket.send('{"instanceName":"'+that.licenseKey+'" , "sessionKey":"'+obj.sessionKey+'"}');
                console.log('{"instanceName":"'+that.licenseKey+'" , "sessionKey":"'+obj.sessionKey+'"}');
                 that.showKey=false
            }
            that.socket.onerror = function (e) {
              that.myService.openAlert({"msg":"Socket is error"});
              that.showKey=true;
    				  console.log("connection Error ()");
				};
            that.socket.onclose = function(e) {
              that.myService.openAlert({"msg":"Socket is onclose"});
              that.showKey=true;
             
            }
            that.socket.onmessage = function(e) {
              let d = JSON.parse(e.data);
              if(d.orderDisplayInfoMap && !d.updatedOrderIds){
                   if(Object.keys(d.orderDisplayInfoMap).length>0){
                    let isNew=true; 

                   for(let v of that.myService.orders){
                      for(let i in d.orderDisplayInfoMap){
                               if(v["itemDisplayInfoList"].length<1)break;
                                let replaceItem={},newItem=[];
                                if(v["orderId"]==d.orderDisplayInfoMap[i]["orderId"] && that.printer.indexOf(i)>=0){
                                   isNew=false;
                                         for(let vv of d.orderDisplayInfoMap[i]["itemDisplayInfoList"]){
                                                 if(vv["replaceItem"]){
                                                  replaceItem[vv["pstId"]]=vv;
                                                 }else{newItem.push(vv)}
                                         }
                                    
                                         for(let ii in v["itemDisplayInfoList"]){
                                              let pstId=v["itemDisplayInfoList"][ii]["pstId"];
                                              if(replaceItem[pstId]){
                                                v["itemDisplayInfoList"][ii]=replaceItem[pstId];
                                              }
                                         }
                                         if(newItem.length>0)
                                            v["itemDisplayInfoList"]= v["itemDisplayInfoList"].concat(newItem);
                                }
                          }
                    }
                    if(isNew){
                      for(let i in d.orderDisplayInfoMap){
                        if(that.printer.indexOf(i)>=0)
                         that.myService.orders=that.myService.orders.concat(d.orderDisplayInfoMap[i])  
                      }
                      
                    }
                  
                    that.setSummary()
                    that.alert.play(); 
                  }
                   if(d.kitchenDisplayUpdate){
                   let kitchenDisplayUpdate=d.kitchenDisplayUpdate;
                    if(kitchenDisplayUpdate["refresh"]){
                        window.location.reload();
                       return true;
                    }
                     for(let v of that.myService.orders){
                             if(v["orderId"]==kitchenDisplayUpdate["orderId"]){
                                if(kitchenDisplayUpdate["orderType"]){
                                  v["orderType"]=kitchenDisplayUpdate["orderType"];
                                  v["tableNum"]=kitchenDisplayUpdate["tableName"] || "";
                                  break;
                                }
                                 for(let vv of v["itemDisplayInfoList"]){
                                         if(kitchenDisplayUpdate["pstIdList"].indexOf(vv["pstId"])>=0){
                                           vv["pstStatus"]=kitchenDisplayUpdate["status"];
                                           if(kitchenDisplayUpdate["status"]=="SERVED")
                                             vv["pstDTime"]=kitchenDisplayUpdate["updateTime"];
                                           }else if(kitchenDisplayUpdate["status"]=="WAITING" || 
                                             kitchenDisplayUpdate["status"]=="PROCESSING"){
                                             vv["pstDTime"]=null;
                                           }
                                       
                                  }
                                  //
                                  
                                  that.setSummary();
                                  that.alert.play();  

                                 break;
                           }
                      }
                  }
                 
               
                }
                
            
            }
	}else{
  this.myService.openAlert({"msg":"WebSocket is not supported"});
	}

  }
    getSesstionKey(host,key){
    return new Promise(function(resolve) {
   let xml='<?xml version="1.0" encoding="UTF-8"?><soapenv:Envelope xmlns:soapenv="http://schemas.xmlsoap.org/soap/envelope/" xmlns:app="http://ws.kpos.com/app"><soapenv:Header/><soapenv:Body><app:ClientInstanceLoginType><app:name>'+key+'</app:name><app:type>KITCHEN_DISPLAY</app:type></app:ClientInstanceLoginType></soapenv:Body></soapenv:Envelope>'
    let type="ClientInstanceLoginResponseType";
    let url="http://"+host+"/kpos/ws/kposService";
         const parser = new DOMParser();
        var xmlhttp = new XMLHttpRequest();
                     xmlhttp.open("POST", url, true);
                     xmlhttp.onreadystatechange = function () {
                          if (xmlhttp.readyState == 4) {
                              if (xmlhttp.status == 200) {
                                  let xmlDoc = parser.parseFromString(xmlhttp.responseText,"text/xml");
                                  resolve(xmlDoc)
                                  }
                          }
                      };
                    xmlhttp.setRequestHeader('Content-Type', 'text/xml');
                   
                    xmlhttp.send(xml);
          

      });
     
}
 
   ngAfterViewInit(){
   if(this.licenseKeyRef)
     this.licenseKeyRef.nativeElement.focus();
  
     this.myService.screenWidth=document.querySelector("main").clientWidth;
     
  

 }
 ngOnDestroy() {
        // unsubscribe to ensure no memory leaks
        this.subscription.unsubscribe();
    }
}
