import { Component, OnInit ,ElementRef,ViewChild,ViewChildren,QueryList} from '@angular/core';
import { FormGroup, FormControl, FormArray, FormBuilder ,Validators} from '@angular/forms';
import {Router,ActivatedRoute}  from '@angular/router';
import { MyService } from '../../my.service';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { Observable,fromEvent,merge } from 'rxjs';
import { map, throttleTime,debounceTime, scan,filter,debounce,bufferTime} from 'rxjs/operators';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { DropdownComponent } from '../../components/dropdown/dropdown.component';
@Component({
  selector: 'app-runner',
  templateUrl: './runner.component.html',
  styleUrls: ['./runner.component.scss']
})
export class RunnerComponent implements OnInit {
   howKey:Boolean=false;
   host:String="";
   title:String="";
   scrollWidth:any=1024;
   
   Number:any;
   Math:any;
   total:any=1;
   page:any=1;
   isShowAll:Boolean=false;
   orders:Array<Object>=[];
    @ViewChild("mainRef") mainRef: ElementRef;
    ele:any ;
    key:any;
    @ViewChildren("sectionRef") sectionRef;
    @ViewChild(DropdownComponent)   columnCom:DropdownComponent;
 /*   @ViewChild(DropdownComponent)   fontCom:DropdownComponent;*/
    @ViewChildren(DropdownComponent) vc: QueryList<DropdownComponent>;
  status:Object={"WAITING":1,"PROCESSING":2,"DONE":3,"SERVED":4,"COMPLETE":5};
   column:Object={"value":"3","_id":"1"}
   font:Object={"value":"1","_id":"2"}
   dateTime:any=""; 
   licenseKey:String="";
  constructor(public translate: TranslateService,public myService:MyService,
    public router:Router,public xml2json: NgxXml2jsonService,public route:ActivatedRoute 
    ) {
  	this.Number=Number;
    this.Math=Math;
  }
    init(){
  this.column=window.localStorage.getItem("COLUMN")?JSON.parse(window.localStorage.getItem("COLUMN")):{"value":"3","_id":"1"};
  this.font=window.localStorage.getItem("FONT")?JSON.parse(window.localStorage.getItem("FONT")):{"value":"1","_id":"2"};
  

  this.licenseKey=window.localStorage.getItem("runnerLicense");
    }
  exit(){
   window.parent.postMessage("exit","*")
 }
  blurColumn(){setTimeout(()=>{this.vc["_results"][0].toggle(false)},200)}

  columnEvent(event){
      this.column=event;
    this.checkedPage(false);
    window.localStorage.setItem("COLUMN",JSON.stringify(event));
    this.vc["_results"][0].toggle(false)
  }
  clickColumn(){this.vc["_results"][0].toggle();}

  blurFont(){setTimeout(()=>{this.vc["_results"][1].toggle(false)},200)}
  fontEvent(event){

  window.localStorage.setItem("FONT",JSON.stringify(event));
  this.checkedPage(false);  
  
  this.font=event;
  this.vc["_results"][1].toggle(false)
}
  clickFont(){this.vc["_results"][1].toggle()}

  done(order){
    let tempItem=JSON.parse(JSON.stringify(order));
   if(this.status[order["pstStatus"]]>2){
     tempItem["pstStatus"]=tempItem["pstStatus"];  
   }else{
tempItem["pstStatus"]="PROCESSING"
   }
 

    tempItem["pstId"]=[];
   for(let v of order["itemDisplayInfoList"]){
      tempItem["pstId"].push(v["pstId"])
    }
   
     this.myService.sendMessage(tempItem);
  }
  refresh(){
    window.location.reload();
  }
 process(item,orderId){
    let tempItem=JSON.parse(JSON.stringify(item));
    tempItem["orderId"]=orderId;
    tempItem["pstId"]=[tempItem.pstId];
   this.myService.sendMessage(tempItem);
 }
  ngOnInit() {
    if(!window.localStorage["runnerLicense"])
     return true;
    this.init();
    const keyup$=fromEvent(document,"keyup");
    const scroll$=fromEvent(this.mainRef.nativeElement,"scroll");
    scroll$.pipe(throttleTime(200)).subscribe(event=>{
            
           
              let startValue=this.mainRef.nativeElement.scrollLeft%this.myService.screenWidth;
              let endValue=startValue;
              if(startValue>0){
                
                  
             
                  endValue=this.myService.screenWidth-startValue;
                  if(this.key=="n"){
                  this.mainRef.nativeElement.scrollLeft=this.mainRef.nativeElement.scrollLeft+endValue;  
                   }else if(this.key=="p"){
                     this.mainRef.nativeElement.scrollLeft=this.mainRef.nativeElement.scrollLeft-startValue;
                   }
      
               
              }
          

    })
  
    keyup$.pipe().subscribe(event=>{
    
      this.key=event['key'];
      if(this.key=="v"){
        this.isShowAll=!this.isShowAll;
      	return ;
      }
      if(this.key=="n"){
         if(document.activeElement && document.activeElement["tagName"]!="SECTION"){
         
              this.ele=this.sectionRef.first.nativeElement;
            
       }else{
         this.ele=document.activeElement.nextElementSibling;
         
       }
         
       }else if(this.key=="p"){
         if(document.activeElement && document.activeElement["tagName"]!="SECTION"){
           if(this.sectionRef.first)
           this.ele=this.sectionRef.first.nativeElement;
            
         }else{
           this.ele=document.activeElement.previousElementSibling;
             
         }
       
       
       }else if(this.key=="i"){
         if(document.querySelector(":focus section")){
           this.ele=document.querySelector(":focus section");
            
        }else{
          this.ele=document.activeElement.parentElement;
        }
         
       }
       
       if(this.ele)this.ele.focus();
       if(document.activeElement && document.activeElement["tagName"]=="SECTION" && this.key=="u"){
         if(document.activeElement.nextElementSibling){
           this.ele.focus();//this.key="n";
         }else if(document.activeElement.previousElementSibling){
           this.ele.focus();//this.key="p";
         }
          
         
         
       }
      
       
    })

    }
undo(){
  this.myService.sendMessage({"sign":"UNDO"});
}

 checkedPage(arg=true){
   
 
        this.scrollWidth=this.mainRef.nativeElement.scrollWidth;
        this.total=Math.floor(this.scrollWidth / this.myService.screenWidth);
         this.page=1+(Math.floor(this.mainRef.nativeElement.scrollLeft/this.scrollWidth));
         this.mainRef.nativeElement.scrollLeft=(this.page-1)*this.scrollWidth;
         setTimeout((()=>{
              if(this.sectionRef.first && this.sectionRef.first.nativeElement)
              this.sectionRef.first.nativeElement.focus();
         }).bind(this),300)
        
    
 }
 ngAfterViewInit(){
 setTimeout(arg=>{ 
    this.myService.screenWidth=document.querySelector("main").clientWidth;
   },1200)
 }
 isElementInViewport (el) {
    var rect = el.getBoundingClientRect();
    return (
        //rect.top >= 0 &&
        rect.left >= 0 
       // rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) && /*or $(window).height() */
       // rect.right <= (window.innerWidth || document.documentElement.clientWidth) /*or $(window).width() */
    );
}
 nextPage(arg){
   this.page=Math.ceil(this.mainRef.nativeElement.scrollLeft/this.myService.screenWidth)+1+arg;
   this.mainRef.nativeElement.scrollLeft=(this.page-1)*this.myService.screenWidth;
}

 language(){
        if(this.translate.currentLang=="en"){
           this.translate.use('zh-cn');
           this.translate.currentLang="zh-cn";
      }else{
           this.translate.use('en');
           this.translate.currentLang="en";

      }
      
      window.localStorage.setItem("LG",this.translate.currentLang);
      
    }
 }
