import { Directive,Input,ElementRef } from '@angular/core';
import * as moment from 'moment';
@Directive({
  selector: '[appMyClock]'
})
export class MyClockDirective {
  	moment:any;	
    @Input() time: any=0;
    @Input() endTime:any=0;
    @Input() format: string='h:mm:ss A';
    @Input() stop:Boolean=false;
    clearTime:any;
  constructor(private el:ElementRef) {
     this.moment=moment;
   }
 ngAfterViewInit(){
       if(this.time==this.endTime){
   	    	this.el.nativeElement.innerText=moment().format(this.format);	
   	    	this.clearTime=setInterval(arg=>{
   			
   			this.el.nativeElement.innerText=moment().format(this.format);	
   		},1000)
       }else if(this.time>0){
   	     let temp=0;		
   	     if(this.endTime>0){
   	      temp=this.endTime-this.time;		
   	     }else{
   	      temp=new Date().getTime()-this.time;	
   	     }
   	      this.el.nativeElement.innerText=this.countDown(temp);
          this.clearTime=setInterval(arg=>{
          if(!this.endTime || this.endTime<=0){
            temp+=1000;
           this.el.nativeElement.innerText=this.countDown(temp);//moment(new Date().getTime()-this.time).format(this.format);    
          }
   			  
         //if(this.endTime>0){window.clearInterval(this.clearTime)}  
   		  },1000)
   		 
   	    
        }else if(this.time==0 && this.endTime>0){
   	    	let temp=this.endTime-new Date().getTime();	
   	    	this.el.nativeElement.innerText=this.countDown(temp);
	   	    	this.clearTime=setInterval(arg=>{
	   			temp-=1000;
	   			this.el.nativeElement.innerText=this.countDown(temp);//moment(new Date().getTime()-this.time).format(this.format);	
	   		  },1000)	
	   	    if(temp<=0)window.clearInterval(this.clearTime);	
   	    }
   }
   pad2(num, n) { 
     return (Array(n).join('0') + num).slice(-n); 
   } 
  countDown = (second) => {
  	let time=second;
        let d=~~(time/(60*60*1000*24))
        let h=~~(time/(60*60*1000)%24);
     	let m=~~(time/(60*1000)%60);
        let s=~~(time/(1000)%60);
        return (d>0?d+" ":"")+(h>0?h+":":"")+this.pad2(m,2)+":"+this.pad2(s,2);
};
}
