import { Component, OnInit ,ElementRef,ViewChild,ViewChildren,QueryList} from '@angular/core';
import { FormGroup, FormControl, FormArray, FormBuilder ,Validators} from '@angular/forms';
import {Router,ActivatedRoute}  from '@angular/router';
import { MyService } from '../../my.service';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { Observable,fromEvent,merge } from 'rxjs';
import { map, throttleTime,debounceTime, scan,filter,debounce,bufferTime} from 'rxjs/operators';
import * as moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { SearchDropComponent } from '../../components/search-drop/search-drop.component';
@Component({
  selector: 'app-kitchen-search',
  templateUrl: './kitchen-search.component.html',
  styleUrls: ['./kitchen-search.component.scss']
})
export class KitchenSearchComponent implements OnInit {
 @ViewChild(SearchDropComponent)   searchCom:SearchDropComponent;

  showKey:Boolean=true;
  search:Object={"value":""};
  isClose:Boolean=true;
   scrollWidth:any=1024;
   
   column:Object={"value":"3","_id":"3"}
   font:Object={"value":"1","_id":"1"}
  blurTime:any;
  keyWords:Object={};
      @ViewChild("mainRef") mainRef: ElementRef;
  @ViewChild("searchRef") searchRef: ElementRef;
 Number:any;
 Math:any;
  constructor(public translate: TranslateService,public myService:MyService
     ) { 
this.Number=Number;
this.Math=Math;
  }
  
  ngOnInit() {
      this.init();
  	 const searchRef$=fromEvent(this.searchRef.nativeElement,"focus");
  	  searchRef$.subscribe(event=>{
  	  	this.searchCom.toggle(true);
        this.showKey=true;
  	  })
      /*for(let i in this.myService.orders){
          console.log(this.myService.orders[i])
          this.keyWords["name"]="item";
      }*/
  }
   init(){
  this.column=window.localStorage.getItem("COLUMN")?JSON.parse(window.localStorage.getItem("COLUMN")):{"value":"3","_id":"1"};
  this.font=window.localStorage.getItem("FONT")?JSON.parse(window.localStorage.getItem("FONT")):{"value":"1","_id":"2"};
 }
  process(item,orderId){
    let tempItem=JSON.parse(JSON.stringify(item));
    tempItem["orderId"]=orderId;
    tempItem["pstId"]=[tempItem.pstId];
   this.myService.sendMessage(tempItem);
 }
  searchEvent(event){
    this.search["value"]=event["value"];
    this.showKey=false;
    this.searchCom.toggle(false);
  }
  blurSearch(){
  // if(this.isClose)
    this.blurTime=setTimeout(()=>{this.searchCom.toggle(false);this.showKey=false},200)
  }
   ngAfterViewInit(){

   	setTimeout(event=>{this.searchRef.nativeElement.focus()},100)
   	 
   }
   nextPage(arg){
       let page=Math.ceil(this.mainRef.nativeElement.scrollLeft/this.myService.screenWidth)+1+arg;
       this.mainRef.nativeElement.scrollLeft=(page-1)*this.myService.screenWidth;
    
 }
   returnEvent(event){
     console.log(event);
     this.searchRef.nativeElement.focus();
      if (event=="Backspace") {
       let len=this.search["value"].length;
             if(len>0)
             this.search["value"]=this.search["value"].substr(0,len-1)
     }else{
       this.search["value"]+=event;  
     } 
     
    
   }

}
